<template>
  <div id="ipi-board">
    <div class="left" style="text-align:center">
      <div class="title">
        <h2>库存监测</h2>
      </div>
      <div class="left-content">
        <h3>请选择店铺</h3>
        <!-- 菜单 -->
        <el-menu
          default-active="0-0-0"
          class="el-menu-vertical-demo"
          unique-opened
        >
          <el-submenu
            :index="index + ''"
            v-for="(item, index) in menuPlatFormList"
            :key="index"
          >
            <template slot="title">
              <span
                style="display:inline-block;width:100%"
                @click="onceClick(index)"
                >{{ item }}</span
              >
            </template>
            <el-submenu
              :index="index + '-' + index2"
              v-for="(item2, index2) in showCountryList"
              :key="index2"
            >
              <template slot="title">
                <span
                  style="display:inline-block;width:100%"
                  @click="secondClick(index2)"
                  >{{ item2 }}</span
                >
              </template>
              <el-menu-item
                :index="index + '-' + index2 + '-' + index3"
                v-for="(item3, index3) in lastSiteList"
                :key="index3"
                @click="thirdClick(item, item2, item3)"
              >
                <span>{{ item3 }}</span>
              </el-menu-item>
            </el-submenu>
          </el-submenu>
        </el-menu>
      </div>
    </div>
    <div class="right">
      <div class="table">
        <el-button
          size="mini"
          :style="{
            'background-color': curIndex === 0 ? '#66b1ff' : '',
            color: curIndex === 0 ? '#fff' : ''
          }"
          @click="typeClick('钢木')"
          >钢木</el-button
        >
        <el-button
          size="mini"
          :style="{
            'background-color': curIndex === 1 ? '#66b1ff' : '',
            color: curIndex === 1 ? '#fff' : ''
          }"
          @click="typeClick('魔片')"
          >魔片</el-button
        >

        <el-table
          :data="stockMonitorList"
          border
          style="width: 100%"
          max-height="700"
          v-loading="tableLoading"
          element-loading-text="拼命加载中"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(255, 255, 255, 0.8)"
        >
          <af-table-column width="130" prop="0" label="spu"></af-table-column>
          <af-table-column width="160" prop="1" label="sku"></af-table-column>
          <el-table-column prop="2" label="fba"></el-table-column>
          <el-table-column
            :prop="3 + index + ''"
            :label="item"
            v-for="(item, index) in containerList"
            :key="index"
          ></el-table-column>
          <el-table-column
            :prop="3 + containerList.length + index + ''"
            :label="item"
            v-for="(item, index) in tableFinalList"
            :key="index + 'a'"
          ></el-table-column>
        </el-table>
        <!-- <u-table
          :data="stockMonitorList"
          border
          style="width: 100%"
          max-height="700"
          use-virtual
          v-loading="tableLoading"
          element-loading-text="拼命加载中"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(255, 255, 255, 0.8)"
        >
          <u-table-column prop="0" label="spu"></u-table-column>
          <u-table-column prop="1" label="sku"></u-table-column>
          <u-table-column prop="2" label="fba"></u-table-column>
          <u-table-column
            :prop="3+index+''"
            :label="item"
            v-for="(item,index) in containerList"
            :key="index"
          ></u-table-column>
          <u-table-column
            :prop="3+containerList.length+index+''"
            :label="item"
            v-for="(item,index) in tableFinalList"
            :key="index+'a'"
          ></u-table-column>
        </u-table>-->
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="50"
          layout="prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      // 选中按钮
      curIndex: 0,
      // 左侧导航栏菜单
      menuPlatFormList: [],
      menuCountryList: [],
      menuSiteList: [],
      showCountryList: [],
      showSiteList: [],
      lastSiteList: [],
      // 选中的菜单项
      checkedPlatform: "",
      checkedCountry: "",
      checkedSite: "",
      checkedType: "钢木",
      // 库存监测列表
      stockMonitorList: [],
      // 货柜列表
      containerList: [],
      // 表格数据最后五栏
      tableFinalList: [
        "在途数量",
        "库存合计",
        "均单量",
        "预计售空日期",
        "提示"
      ],
      // 表格加载中显示loading
      tableLoading: true,
      // 分页相关
      total: 0,
      currentPage: 1
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 获取菜单栏数据
    getMenuList() {
      this.$api.getStockMenuList().then(res => {
        // console.log(res);
        this.menuPlatFormList = res.data.platform_list;
        this.menuCountryList = res.data.country_list;
        this.menuSiteList = res.data.site_list;

        // 页面刷新 先默认展示胤佑的 同时在导航那里添加default-active="0-0-0"
        this.showCountryList = this.menuCountryList[0];
        this.showSiteList = this.menuSiteList[0];
        this.lastSiteList = this.showSiteList[0];
        this.checkedPlatform = this.menuPlatFormList[0];
        this.checkedCountry = this.showCountryList[0];
        this.checkedSite = this.lastSiteList[0];
        // 获取货柜号
        this.getContainerList();
        this.thirdClick(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite
        );
      });
    },
    // 获取货柜号
    getContainerList() {
      this.$api
        .stockMonitor(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          this.checkedType,
          this.currentPage
        )
        .then(res => {
          console.log(res);
          this.containerList = res.data.header;
        });
    },
    onceClick(index) {
      this.showCountryList = [];
      this.showSiteList = [];
      this.showCountryList = this.menuCountryList[index];
      this.showSiteList = this.menuSiteList[index];
    },
    secondClick(index) {
      console.log(index);
      this.lastSiteList = [];
      this.lastSiteList = this.showSiteList[index];
    },
    thirdClick(platform, country, site) {
      this.checkedPlatform = platform;
      this.checkedCountry = country;
      this.checkedSite = site;
      this.tableLoading = true;
      this.$api
        .stockMonitor(
          platform,
          country,
          site,
          this.checkedType,
          this.currentPage
        )
        .then(res => {
          console.log(res);
          this.tableLoading = false;
          // this.containerList = res.data.header;
          this.stockMonitorList = res.data.data;
          this.total = res.data.all_number;
        });
    },
    typeClick(type) {
      if (type === "钢木") {
        this.curIndex = 0;
      } else if (type === "魔片") {
        this.curIndex = 1;
      }
      this.checkedType = type;
      this.thirdClick(
        this.checkedPlatform,
        this.checkedCountry,
        this.checkedSite
      );
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.thirdClick(
        this.checkedPlatform,
        this.checkedCountry,
        this.checkedSite
      );
    }
  },
  created() {
    this.getMenuList();
  },
  mounted() {}
};
</script>
<style scoped>
#ipi-board {
  width: 100vw;
  height: 92vh;
  position: relative;
  /* margin-top: 2vh; */
}
.left {
  position: absolute;
  width: 16vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  border-right: 1px solid black;
  top: 0;
  left: 0;
  overflow: auto;
}
.right {
  position: absolute;
  top: 0;
  left: 18vw;
  width: 80vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  padding: 24px;
  overflow: auto;
}
.left-content h3 {
  margin-top: 2vh;
  text-align: center;
}
.el-table {
  margin-top: 12px;
}
</style>



